.video-gallery {
  margin: 0 auto;
  max-width: 320px;
  box-sizing: border-box;
  position: relative;

  @media (min-width: $tablet-width) {
    max-width: 768px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    max-width: 1200px;
    margin: 0 auto;
  }
}

.video-gallery__text-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  padding: 15px 26px 38px 28px;

  ::before {
    content: "";
    display: inline-block;
    margin: 0 auto;
    position: absolute;
    top: -5px;
    left: 0;
    height: 1px;
    width: 320px;
    background-color: #e5e5e5;
  }

  @media (min-width: $tablet-width) {
    padding: 58px 162px 66px 161px;
    max-width: 768px;
    margin: 0 auto;

    ::before {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 62px 378px 64px 377px;
    max-width: 768px;
    margin: 0 auto;

    ::before {
      display: none;
    }
  }
}

.video-gallery__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;

  margin: 0;
  padding-bottom: 26px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 26px;
    padding-bottom: 30px;
  }
}

.video-gallery__description {
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;

  margin: 0;

  @media (min-width: $tablet-width) {
    padding-bottom: 20px;
  }
}

.video-gallery__content {
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  position: relative;

  @media (min-width: $tablet-width) {
    padding-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 84px;
  }
}

.video-gallery__button {
  position: absolute;
  content: "";
  background-color: transparent;
  border: none;
  background-image: url("../img/gallery/play-button.svg");
  width: 48px;
  height: 54px;

  margin-left: 10px;

  @media (min-width: $tablet-width) {
    margin-left: 0;
  }
}
