.main-nav {
  position: relative;
  background-color: #81b3d2;
  width: 100%;
  margin: 0 auto;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
    box-shadow: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    margin: 0 auto;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 55px;
  height: 56px;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.main-nav__toggle:hover, .main-nav__toggle:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 14px;
  width: 22px;
  height: 4px;
  background-color: #81b3d2;
  background-repeat: space repeat;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#ffffff, 30%);
  box-shadow: 0 8px 0 0 fade(#81b3d2, 30%), 0 16px 0 0 fade(#81b3d2, 30%);
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  left: auto;
  right: 0;
  background-color: #81b3d2;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 28px;
  left: 10px;
  width: 30px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover {
  background-color: #669ec0;
}
.main-nav--opened .main-nav__toggle:active {
  background-color: #5496bd;
  color: #ffffff;
  opacity: 0.3;
}

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .site-list__item {
    display: block;
  }
}
.main-nav.main-nav--closed {
  background-color: #ffffff;
  padding-top: 56px;
}

